import uCharts from '../../components/u-charts/u-charts.js'
var _self;
var canvaLineA = null;
var mqtt = require('../../common/js/mqtt.min.js')
export default {
	data() {
		return {
			time_range: ['08:25', '09:45', '10:22', '12:12', '13:25', '16:25', '18:25'],
			dateList: [{
					date: '2020年2月19日',
					time: ['09:55:16', '10:25:26', '09:55:16']
				},
				{
					date: '2020年2月20日',
					time: ['09:55:16', '10:25:26', '09:55:16', '09:55:16']
				},
			],
			bpm: '86BPM',

			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			client: null,
			patientList: [],
			patient: null,
			heart_rate_list: [],
			categories: []
		};
	},
	onLoad() {
		_self = this;
		// console.log(this.getNowTime())
		this.fetchPatientList()
		// _self.getServerData();
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		// 创建socket连接
		let client = mqtt.connect('wxs://eztbs.oicp.net:8888/mqtt', {
			clientId: 'adfas',
			username: 'admin',
			password: 'admin'
		})
		this.client = client
		this.getSocket()
	},

	methods: {
		// 获取socket数据
		getSocket() {
			this.client.on('connect', () => {
				console.log('mqtt连接成功')
				this.client.subscribe('/statues', (err) => {
					if (!err) {
						console.log('订阅成功')
					}
				})
			})
			// 客户端连接错误事件
			this.client.on('error', error => {
				console.log(error)
			})
			// 监听接收消息事件
			this.client.on('message', (topic, message) => {
				// console.log('收到消息：' + message.toString())
				let data = message.toString()
				console.log('收到消息', data)
				let dataArr = JSON.parse(data)
				if (dataArr.length > 1) {
					for (let item of dataArr) {
						if (this.patient && item.mac == this.patient.mac) {
							// console.log(item)
							let heart_rate = parseInt(item.rawData.slice(14, 15), 16)
							let timer = item.timestamp.slice(12, 19)
							this.heart_rate_list.push(heart_rate)
							// let randomData = Math.random() * 100
							// this.heart_rate_list.push(randomData)
							this.categories.push(timer)
							if (this.heart_rate_list.length > 6) {
								this.heart_rate_list.shift()
								this.categories.shift()
							}
							// 初始化图表实例
							_self.showLineA("myChart")
							// updateData更新图表
							canvaLineA.updateData({
								categories: this.categories,
								series: [{
									name: '实时心率',
									data: _self.heart_rate_list
								}],
							})
						}
					}
				}
			})
		},

		showLineA(canvasId) {
			// 图表实例和配置
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				colors: ['#FFFFFF'],
				type: 'line',
				fontSize: 12,
				dataLabel: false,
				dataPointShape: true,
				background: '#24C789',
				pixelRatio: _self.pixelRatio,
				categories: '',
				series: [{
					name: '实时心率',
					data: ''
				}],
				animation: false,
				xAxis: {
					gridColor: '#FFF',
					gridType: 'dash',
					disableGrid: true,
					axisLine: false,
					fontColor: '#FFF',
					// boundaryGap: 'justify'
				},
				yAxis: {
					data: [{
						position: 'right',
						fontColor: '#FFF',
						axisLineColor: '#24C789',
						min: 0,
						max: 300,
					}],
					gridType: 'dash',
					gridColor: '#FFF',
					splitNumber: 6,
					dashLength: 2,
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					},
					tooltip: {
						gridType: 'dash',
						dashLength: 5,
						gridColor: '#24C789'
					}
				}
			});
		},

		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},

		// picker @change事件
		bindPickerChange(e) {
			// console.log(e)
			for (let item of this.patientList) {
				if (item.id === Number(e.detail.value) + 1) {
					this.fetchPatientInfo(item.id)
				}
			}
		},
		// 获取患者列表(ID)
		async fetchPatientList() {
			await uni.request({
				url: 'https://ciai.le-cx.com/api/patient/patientList',
				success: res => {
					this.patientList = res.data.data
				}
			})
		},
		// 获取患者信息
		async fetchPatientInfo(id) {
			await uni.request({
				url: `https://ciai.le-cx.com/api/patient/info?id=${id}`,
				success: res => {
					this.patient = res.data.data
				},
			})
		},

		// 获取当前时间
		getNowTime() {
			let now = new Date()
			let hour = now.getHours()
			let minute = now.getMinutes()
			let second = now.getSeconds()
			hour = hour < 10 ? '0' + hour : hour
			minute = minute < 10 ? '0' + minute : minute
			second = second < 10 ? '0' + second : second
			let now_time = `${hour}:${minute}:${second}`
			return now_time
		},

		getServerData() {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					console.log(res.data.data)
					let LineA = {
						categories: [],
						series: []
					};
					//这里后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					LineA.categories = res.data.data.LineA.categories;
					// LineA.series = res.data.data.LineA.series;
					// 只筛选了最后一条数据进行可视化
					LineA.series = res.data.data.LineA.series;
					LineA.series = [LineA.series.pop()];
					_self.showLineA("myChart", LineA);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},

	},
}
